.example__container {
    padding: 0 20px 34px;
    width: 320px;
    margin-top: 74px;
    box-sizing: border-box;
}

.example__container--ident {}

.example {
    background-color: @gray-color;
}

.example__heading {
    font-family: "Oswald", "Arial", sans-serif;
    font-size: 36px;
    line-height: 60px;
    font-weight: 400;
    color: @black-color;
    margin: 0 0 23px;
    padding-top: 36px;
    padding-top: 18px;
    margin: 0 0 -53px;
    padding-left: 21px;
}

.example__description {
    font-family: "Arial", sans-serif;
    font-size: 14px;
    line-height: 18px;
    color: @text-color;
    margin: 0 0 25px;
    padding-right: 1px;
}

.example__list {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: space-between;
    margin: 0 0 33px;
}

.example__item {
    width: 122px;
    text-align: center;
    padding: 13px 0 18px;
    border: 1px solid #cdcdcd;
    border-radius: 5px;
    position: relative;
}

.example__item-title {
    font-family: "Oswald", "Arial", sans-serif;
    font-size: 24px;
    line-height: 24px;
    color: @black-color;
    text-transform: uppercase;
    margin: 0;
}

.example__item-subtitle {
    font-family: "Arial", sans-serif;
    font-size: 12px;
    line-height: 12px;
    color: #444;
    margin: 0 auto;
    display: block;
    text-align: center;
    min-width: 80px;
    position: absolute;
    bottom: -15px;
    transform: translateX(-50%);
    left: 50%;
    background-color: @gray-color;
    padding: 0 9px;
}

.example__prise {
    font-family: "Oswald", "Arial", sans-serif;
    font-size: 14px;
    line-height: 20px;
    color: @black-color;
    text-transform: uppercase;
    text-align: center;
    margin: 0 0 0 3px;
    padding-right: 7px;
}

.example__photo {
    left: -19px;
    top: -3px;
    position: relative;
    margin-top: 37px;
    min-width: 320px;
    height: 240px;
}

@media (min-width: @tablet-width) {
    .example {
        background-color: @white-color;
    }
    .example__price-wrapper {
        display: flex;
        width: 708px;
        margin: 0 auto;
    }
    .example__container {
        border-top: 1px solid #e6e6e6;
        padding: 0px 9px 74px;
        width: 724px;
    }
    .example__heading {
        font-size: 60px;
        line-height: 60px;
        padding-top: 16px;
        margin: 0;
        display: inline;
    }
    .example__description {
        font-size: 16px;
        line-height: 24px;
        width: 700px;
        margin-bottom: 52px;
    }
    .example__list {
        display: flex;
        justify-content: space-between;
        width: 408px;
    }
    .example__item {
        width: 164px;
        margin-right: 40px;
    }
    .example__item-title {
        font-size: 30px;
        line-height: 37px;
    }
    .example__item-subtitle {
        font-size: 14px;
        line-height: 14px;
        white-space: nowrap;
        bottom: -8px;
        background-color: #fff;
    }
    .example__prise {
        width: 192px;
        margin: 0 0 0 auto;
        font-size: 20px;
        text-align: right;
        padding-top: 8px;
        line-height: 29px;
    }
    @media (min-width: @desktop-width) {
        .example__container {
            min-width: 1300px;
            padding: 0 40px;
            min-height: 453px;
            box-sizing: border-box;
            border: 0;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            position: relative;
            z-index: 1;
            background: linear-gradient(to right, #f2f2f2 75%, #eaeaea 27.2%);
        }

    }


    .example__cat {
        position: relative;
        right: 67px;
        bottom: 144px;
    }

    .example__cat::before {
       content: "";
       width: 326px;
       height: 643px;
       background: url(../img/after-desktop@1x.png);
       position: absolute;
       left: 391px;
       top: -48px;
       background-position: -365px;
       background-position-y: 1px;
       z-index: 1;
       background-repeat: no-repeat;
   }




   .example__wrapper {
    width: 50%;
    padding: 72px 0 0 0;
}
.example__heading {
    margin: 0 40px;
    padding: 9px 0 0;
    display: inline-block;
}
.example__description {
    margin-bottom: 68px;
    width: 435px;
}
.example__price-wrapper {
    width: 435px;
    flex-wrap: wrap;
    margin: 0;
}
.example__list {
    margin-bottom: 48px;
}
.example__prise {
    width: 435px;
    text-align: left;
}
.example__prise span {
    margin-left: 59px;
}

.example__photo {
    left: 30px;
    top: -87px;
    position: absolute;
    margin-top: 37px;
    min-width: 676px;
    height: 522px;
    min-width: 679px;
    clip: rect(auto, 361px, auto, auto)
}



.example {
    position: relative;
    margin: 0 auto;
    max-width: 1300px;
}

.example__item-subtitle {
    background-color: #f2f2f2;
}

}
