.form__wrapper {
  width: 280px;
  margin: 0 auto;
}
.form__heading_info {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  color: #111;
  background-color: #ffffff;
  display: inline-block;
  position: relative;
  z-index: 1;
  margin: 0 0 34px;
  width: 181px;
  padding-right: 21px;
}
.form__info-container {
  margin: 0;
  padding: 0;
  list-style: none;
  margin: 0 auto 23px;
}
.form__list-item {
  display: flex;
  position: relative;
  align-items: center;
  margin-bottom: 15px;
}
.form__input-mail,
.form__input-phone {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  color: #222;
