  text-transform: uppercase;
  margin-right: 11px;
  width: 73px;
  width: 91px;
  margin-right: 13px;
}
.form__button-mail {
  border: none;
  background-color: #ffffff;
  position: absolute;
  right: 8px;
}
.form__button-phone {
  border: none;
  background-color: #ffffff;
  position: absolute;
  right: 8px;
