}
.form__button-mail svg {
  width: 19px;
  height: 15px;
}
.form__button-phone svg {
  width: 22px;
  height: 25px;
}
.form__fieldset--comment {
  position: relative;
}
.form__fieldset--comment::after {
  content: "";
  position: absolute;
  height: 3px;
  background-color: #68b738;
  left: 50%;
  width: 50vw;
  top: 15px;
  right: 0;
}
.form__heading-comment {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 24px;
