  font-size: 14px;
  line-height: 16px;
  color: #444;
  text-align: center;
  padding-top: 6px;
}
.form__input-phone {
  padding: 12px;
}
.form__input-text:active {
  border: none;
  box-shadow: inset 0 0 0 2px #444444;
}
.form__input-text:focus,
.form__input-text:invalid {
  border: none;
  box-shadow: inset 0 0 0 2px #ff8282;
  outline: none;
}
.form__input-text:hover {
  box-shadow: inset 0 0 0 1px #e6e6e6;
}
.form__radio-input + label:active {
  opacity: 0.3;
  outline: none;
}
.form__radio-input + label:hover::before {
  border-color: #bababa;
}
.form__radio-input + label:focus::before {
  border-color: #bababa;
}
.form__checkbox-input + label:active {
  opacity: 0.3;
  outline: none;
}
.form__checkbox-input + label:hover::before {
  border-color: #bababa;
}
.form__checkbox-input + label:focus::before {
  border-color: #bababa;
}
.form__input-mail:active {
  box-shadow: inset 0 0 0 2px #444;
  outline: none;
}
.form__input-mail:hover,
.form__input-mail:focus {
  box-shadow: inset 0 0 0 1px #e6e6e6;
  outline: none;
}
.form__input-mail:active + .form__button-mail {
  fill: #68b738;
  outline: none;
}
.form__input-mail:focus,
.form__input-mail:invalid + .form__button-mail svg {
  fill: #ff8282;
  border: none;
  box-shadow: inset 0 0 0 2px #ff8282;
  outline: none;
}
.form__input-phone:active {
  box-shadow: inset 0 0 0 2px #444;
  outline: none;
}
.form__input-phone:hover,
.form__input-phone:focus {
  box-shadow: inset 0 0 0 1px #e6e6e6;
  outline: none;
}
.form__input-phone:active + .form__button-phone {
  fill: #68b738;
  outline: none;
}
.form__input-phone:focus,
.form__input-phone:invalid + .form__button-phone svg {
  fill: #ff8282;
  border: none;
  box-shadow: inset 0 0 0 2px #ff8282;
  outline: none;
}
